<template>
  <transition name="slide">
    <div class="address-main">
      <c-nav-bar class="address-nav-bar">
        <i slot="left" class="nav-bar-left iconfont icon-fanhui1"></i>
        <span slot="center" class="nav-bar-center">
          地区选择
        </span>
      </c-nav-bar>
      <div class="address-content">
        <div class="province-list" ref="provinceList">
          <div>
            <div class="item" v-for="(val,index) in get_Province" :class="{ 'active':val.codeCode == (chooseCity[0] && chooseCity[0].codeCode)}" :key="index" @click="provinceClick(val,index)">
              {{ val.codeName }}
            </div>
          </div>
        </div>
        <div class="city-list" ref="cityList">
          <div>
            <div class="item" :class="{'active':val.codeCode == (chooseCity[1] && chooseCity[1].codeCode)}" v-for="(val,index) in city" :key="index" @click="cityClick(val)">
              {{ val.codeName }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import { mapGetters } from 'vuex'
import BScoll from 'better-scroll'
export default {
  data() {
    return {
      provinceScroll: '',
      cityScroll: '',
      chooseCity: []
    }
  },
  mounted() {
    setTimeout(() => {
      this.init()
      console.log(this.$route.params.id)
    },20)
  },
  computed: {
    ...mapGetters(['get_Province','get_City']),
    city() {
      if (this.chooseCity[0] && this.chooseCity[0].codeCode) {
        let _codeCode = this.chooseCity[0].codeCode
        return this.get_City.filter((city) => {
          return city.parentCode === _codeCode
        })
      } else {
        return []
      }
    }
  },
  methods: {
    init() {
      this.provinceScroll = new BScoll(this.$refs.provinceList,{
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
      this.cityScroll = new BScoll(this.$refs.cityList,{
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
    },
    provinceClick(val) {
      this.chooseCity.splice(0, 1, val)
    },
    cityClick(val) {
      this.chooseCity.splice(1, 1, val)
      console.log(val)
      let _chooseCity = this.chooseCity
      let provinceName = _chooseCity[0].codeName
      let provinceCode = _chooseCity[0].codeCode
      let cityName = _chooseCity[1].codeName
      let cityCode = _chooseCity[1].codeCode
      let _data = {
        provinceName,provinceCode,cityName,cityCode
      }
      console.log(_data)
      this.$store.dispatch( 'change_receiptAreaList_addOne', _data );
      this.$router.back()
    }
  },
}
</script>
<style lang="scss" scoped>
.address-main{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index:1001;
  background: $background-color;
  .address-nav-bar{
    background: #fff;
  }
  .address-content{
    position: fixed;
    top: 44px;
    bottom: 0;
    left: 0;
    right: 0;
    border-top: 1px solid $color-theme;
    display: flex;
    >div {
      width: 50%;
      height: 100%;
      overflow: hidden;
      .item {
        height: 50px;
        line-height: 50px;
        text-align: center;
        white-space: nowrap;
        text-overflow:ellipsis;
        overflow:hidden;
        color:rgb(26,26,26);
        padding: 0 10px;
      }
    }
    .province-list {
      background: $background-color;
      .item{
        &.active{
          color:$color-theme;
          background: #fff;
        }
      }
    }
    .city-list {
      background: #fff;
      .item.active{
        color:$color-theme;
      }
    }
  }
}
</style>
